import React, { useState, useRef, useEffect } from 'react';
import {
  ButtonContainer,
  IconButton,
  Status,
  AudioPlayer,
  InfoButton,
  RecordIcon
} from './styles';

const AudioRecorderPage = ({ triggerReset }) => {
  const [permission, setPermission] = useState(false);
  const [stream, setStream] = useState(null);
  const [isRecording, setIsRecording] = useState(false);
  const [audioURL, setAudioURL] = useState('');
  const mediaRecorder = useRef(null);
  const audioChunks = useRef([]);

  // Reset function
  const resetRecorder = () => {
    //  recorder
    if (mediaRecorder.current) {
      try {
        mediaRecorder.current.stop();
      } catch (e) {
        // 
      }
      mediaRecorder.current = null;
    }

    //  reset 
    if (permission) {
      if (audioURL) {
        URL.revokeObjectURL(audioURL); //  blob URL
      }
      setIsRecording(false);
      setAudioURL('');
      audioChunks.current = [];
    } else {
      //   
      setPermission(false);
      setStream(null);
      setIsRecording(false);
      if (audioURL) {
        URL.revokeObjectURL(audioURL);
      }
      setAudioURL('');
      audioChunks.current = [];
    }
  };

  //  reset
  useEffect(() => {
    if (triggerReset) {
      resetRecorder();
    }
  }, [triggerReset]);

  const getMicrophonePermission = async () => {
    if ('MediaRecorder' in window) {
      try {
        const streamData = await navigator.mediaDevices.getUserMedia({ audio: true });
        setPermission(true);
        setStream(streamData);
      } catch (err) {
        alert(err.message);
      }
    } else {
      alert('');
    }
  };

  const startRecording = async () => {
    if (stream === null) {
      await getMicrophonePermission();
    }

    setIsRecording(true);
    if (audioURL) {
      URL.revokeObjectURL(audioURL); //  URL
    }
    setAudioURL('');
    audioChunks.current = [];

    const media = new MediaRecorder(stream, { type: 'audio/webm' });
    mediaRecorder.current = media;
    mediaRecorder.current.start();

    mediaRecorder.current.ondataavailable = (event) => {
      if (event.data && event.data.size > 0) {
        audioChunks.current.push(event.data);
      }
    };
  };

  const stopRecording = () => {
    setIsRecording(false);
    if (mediaRecorder.current) {
      mediaRecorder.current.stop();

      mediaRecorder.current.onstop = () => {
        const audioBlob = new Blob(audioChunks.current, { type: 'audio/webm' });
        const audioUrl = URL.createObjectURL(audioBlob);
        setAudioURL(audioUrl);
        audioChunks.current = [];
      };
    }
  };

  return (
    <ButtonContainer>
      {/*  */}
      {!permission && (
        <InfoButton onClick={getMicrophonePermission}>
           
        </InfoButton>
      )}

      {/*  */}
      {permission && !isRecording && (
        <InfoButton onClick={startRecording}>
          <RecordIcon recording={false} /> 
        </InfoButton>
      )}

      {/*  */}
      {permission && isRecording && (
        <>
          <IconButton onClick={stopRecording}>  </IconButton>
          <RecordIcon recording={true} />
          <Status>... </Status>
        </>
      )}

      {/*  bar */}
      {permission && !isRecording && audioURL && (
        <AudioPlayer src={audioURL} controls />
      )}
    </ButtonContainer>
  );
};

export default AudioRecorderPage;
