import { PanelContainer, LabelGroup, RangeInput, SettingTitle } from "./styles"; // Import SettingTitle

export default function SettingsPanel({
  playbackSpeed,
  setPlaybackSpeed,
  playbackOptions,
  setPlaybackOptions,
  proficiencyFilter,
  setProficiencyFilter,
  autoProceed,
  setAutoProceed,
  quizScope,
  setQuizScope,
  startQuestionIndex, // New prop
  setStartQuestionIndex, // New prop
  wordRangeCount, // New prop
  setWordRangeCount, // New prop
  isQuizContext, // New prop
}) {
  return (
    <PanelContainer>
      <LabelGroup>
        <SettingTitle>: {playbackSpeed}</SettingTitle>
        <RangeInput
          type="range"
          min="0.5"
          max="2"
          step="0.1"
          value={playbackSpeed}
          onChange={(e) => setPlaybackSpeed(Number(e.target.value))}
        />
      </LabelGroup>

      <LabelGroup>
        <SettingTitle></SettingTitle>
        <div>
          <label>
            <input
              type="checkbox"
              checked={playbackOptions.jp}
              onChange={(e) =>
                setPlaybackOptions({
                  ...playbackOptions,
                  jp: e.target.checked,
                })
              }
            />
            
          </label>
          <label>
            <input
              type="checkbox"
              checked={playbackOptions.ch}
              onChange={(e) =>
                setPlaybackOptions({
                  ...playbackOptions,
                  ch: e.target.checked,
                })
              }
            />
            
          </label>
          <label>
            <input
              type="checkbox"
              checked={playbackOptions.jpEx}
              onChange={(e) =>
                setPlaybackOptions({
                  ...playbackOptions,
                  jpEx: e.target.checked,
                })
              }
            />
            
          </label>
          <label>
            <input
              type="checkbox"
              checked={playbackOptions.chEx}
              onChange={(e) =>
                setPlaybackOptions({
                  ...playbackOptions,
                  chEx: e.target.checked,
                })
              }
            />
            
          </label>
        </div>
      </LabelGroup>
      <LabelGroup>
        <SettingTitle></SettingTitle>
        <label>
          <input
            type="checkbox"
            checked={autoProceed}
            onChange={(e) => setAutoProceed(e.target.checked)}
          />
          
        </label>
      </LabelGroup>

      {!isQuizContext && ( // Conditionally render
        <LabelGroup>
          <SettingTitle>:</SettingTitle>
          <div>
            <label>
              <input
                type="radio"
                name="quizScope"
                value="all"
                checked={quizScope === "all"}
                onChange={(e) => setQuizScope(e.target.value)}
              />
              
            </label>
            <label>
              <input
                type="radio"
                name="quizScope"
                value="low"
                checked={quizScope === "low"}
                onChange={(e) => setQuizScope(e.target.value)}
              />
              
            </label>
            <label>
              <input
                type="radio"
                name="quizScope"
                value="medium"
                checked={quizScope === "medium"}
                onChange={(e) => setQuizScope(e.target.value)}
              />
              
            </label>
            <label>
              <input
                type="radio"
                name="quizScope"
                value="high"
                checked={quizScope === "high"}
                onChange={(e) => setQuizScope(e.target.value)}
              />
              
            </label>
          </div>
        </LabelGroup>
      )}
      {!isQuizContext && ( // Conditionally render startQuestionIndex
        <LabelGroup>
          <SettingTitle>:</SettingTitle>
          <input
            type="number"
            min="1"
            value={startQuestionIndex}
            onChange={(e) => setStartQuestionIndex(Number(e.target.value))}
          />
        </LabelGroup>
      )}

      {!isQuizContext && ( // Conditionally render wordRangeCount
        <LabelGroup>
          <SettingTitle>:</SettingTitle>
          <input
            type="number"
            min="1"
            value={wordRangeCount}
            onChange={(e) => setWordRangeCount(Number(e.target.value))}
          />
        </LabelGroup>
      )}
    </PanelContainer>
  );
}
